import React from "react";
import styles from "./Introduction.module.css";
import { Link } from "react-router-dom";
import video from "../img/video1.mp4";
import "aos/dist/aos.css";
import slide4 from "../img/slide4.png";
import slide3 from "../img/slide3.png";
import html from "../img/html.png";
import css from "../img/css.png";
import js from "../img/javascript.png";
import rct from "../img/react.png";
import firebase from "../img/firebase.png";
import mongo from "../img/mongo.png";
import node from "../img/node.png";
import git from "../img/git.png";
import github from "../img/github.png";
import Tilt from "react-tilt";

const Introduction = () => {
  return (
    <div>
      <video
        autoPlay="autoplay"
        loop
        muted
        className={styles.containerVideo}
        src={video}
      >
        {" "}
      </video>
      <div className={styles.overlay}></div>
      <nav className={styles.linkContainer}>
        <Link className={styles.logoNume} >
          A la Maison!
        </Link>
        <Link className={styles.linkNav} to = "/curiosity">
          Curiosity Corner
        </Link>
        <Link className={styles.linkNav} to="/contact">
          Contact Me
        </Link>
        
        <Link className={styles.linkNav} to = "/projects">
          Projects
        </Link>
        <Link className={styles.linkNav} to="/cards">
          Technologies
        </Link>
      </nav>
      <div className={styles.bigText}>
        <h1>
          Coman <br /> Marian
        </h1>
      </div>

      <div className={styles.section2}>
        <div className={styles.slideContainer0}>
          <h1 data-aos="fade-right">
            I am a <br /> qualified...
          </h1>
        </div>

        <div className={styles.slideContainer1} data-aos="fade-left">
          <h1> ... frontend Developer ...</h1>
          <Tilt className="Tilt" options={{ max: 5, scale: 1.05 }}>
            <div className="Tilt-inner">
              <img src={slide4} alt="man on the computer"></img>
            </div>
          </Tilt>
        </div>

        <div className={styles.teamplayer} data-aos="fade-right">
          <h1>... a good teamplayer ...</h1>
          <Tilt className="Tilt" options={{ max: 5, scale: 1.05 }}>
            <div className="Tilt-inner">
              <img src={slide3} alt="people in team"></img>
            </div>
          </Tilt>
        </div>

        <div className={styles.slider} data-aos="fade">
          <h1 className={styles.titlu}>... with knowledge in ... </h1>
          <div className={styles.slideTrack}>
            <img className={styles.slide} src={html} alt="html" />
            <img className={styles.slide} src={css} alt="css" />
            <img className={styles.slide} src={js} alt="js" />
            <img className={styles.slide} src={rct} alt="rct" />
            <img className={styles.slide} src={firebase} alt="firebase" />
            <img className={styles.slide} src={mongo} alt="mongo" />
            <img className={styles.slide} src={node} alt="node" />
            <img className={styles.slide} src={git} alt="git" />
            <img className={styles.slide} src={github} alt="github" />

            {/* doubled */}

            <img className={styles.slide} src={html} alt="html" />
            <img className={styles.slide} src={css} alt="css" />
            <img className={styles.slide} src={js} alt="js" />
            <img className={styles.slide} src={rct} alt="rct" />
            <img className={styles.slide} src={firebase} alt="firebase" />
            <img className={styles.slide} src={mongo} alt="mongo" />
            <img className={styles.slide} src={node} alt="node" />
            <img className={styles.slide} src={git} alt="git" />
            <img className={styles.slide} src={github} alt="github" />
            {/* doubled */}

            <img className={styles.slide} src={html} alt="html" />
            <img className={styles.slide} src={css} alt="css" />
            <img className={styles.slide} src={js} alt="js" />
            <img className={styles.slide} src={rct} alt="rct" />
            <img className={styles.slide} src={firebase} alt="firebase" />
            <img className={styles.slide} src={mongo} alt="mongo" />
            <img className={styles.slide} src={node} alt="node" />
            <img className={styles.slide} src={git} alt="git" />
            <img className={styles.slide} src={github} alt="github" />
          </div>
        </div>
        <div className={styles.empty}></div>
      </div>
    </div>
  );
};

export default Introduction;
