import React, { useState } from "react";
import styles from "./Project.module.css";
import { AiOutlineMinus, AiOutlinePlus } from "react-icons/ai";



const Project = ({ project }) => {
  const [showAnswer, setShowAnswer] = useState(false);
  return (
    <>
      <div className={styles.containerr} >
        <div  className={styles.sageata}>
            <p>Projecto numero: {project.id }</p>
        </div>
        <h2 className={styles.question}>{project.title}</h2>
        <button
          className={styles.btn}
          onClick={() => setShowAnswer(!showAnswer)}
        >
          {showAnswer ? <AiOutlineMinus /> : <AiOutlinePlus />}
        </button>
        <p className={styles.projectInfo} >{showAnswer && project.info}</p>
        <p>
          {project.link && showAnswer && (
            <a 
              href={project.link}
              target="_blank"
              rel="noreferrer"
              className={styles.projectLink}
            >
              Check it out!
            </a>
          )}
        </p>
        <div className={styles.forma2}>
          <p className={styles.projectCount}>{project.id}</p>
        </div>
      </div>
    </>
  );
};

export default Project;
