import React from "react";
import styles from "./Projects.module.css";
import { Link } from "react-router-dom";
import dataProjects from "./dataProjects";
import Project from "./Project";
import "aos/dist/aos.css";
import Tilt from "react-tilt";

const Projects = () => {
  return (
    <div className = {styles.bossContainer}>
      <div className={styles.main}>
        <nav className={styles.linkContainer}>
          <Link className={styles.logoNume} to="/">
            A la Maison!
          </Link>
          <Link className={styles.linkNav} to="/curiosity">
            Curiosity Corner
          </Link>
          <Link className={styles.linkNav} to="/contact">
            Contact Me
          </Link>
          <Link className={styles.linkNav} to="/cards">
            Technologies
          </Link>
        </nav>
        <h1 className={styles.mainHeader} data-aos="flip-right">
          Projects
        </h1>
        {dataProjects.map((project) => {
          return (
            <Tilt className="Tilt" options={{ max: 11, scale: 1.05 }}>
              <div className="Tilt-inner">
                <Project key={project.id} project={project} />
              </div>
            </Tilt>
          );
        })}
      </div>
    </div>
  );
};

export default Projects;
