import React, { useState, useEffect } from "react";
import styles from "./Curiosity.module.css";
import { Link } from "react-router-dom";
import info from "../img/info.png";

const Curiosity = () => {
  let [showData, setShowData] = useState(false);
  const [photoData, setPhotoData] = useState(null);
  useEffect(() => {
    fetchPhoto();
    async function fetchPhoto() {
      const res = await fetch(
        `https://api.nasa.gov/planetary/apod?api_key=v46ipGA5jPx5tc95GdXxJCwbuTE5SbPsQTwc6JOI`
      );
      const data = await res.json();
      setPhotoData(data);
      console.log(data);
    }
  }, []);

  if (!photoData) return <div>Sorry</div>;
  return (
    <React.Fragment>
      <nav className={styles.linkContainer}>
        <Link className={styles.logoNume} to="/">
          A la Maison!
        </Link>
        <Link className={styles.linkNav} to="/contact">
          Contact Me
        </Link>

        <Link className={styles.linkNav} to="/projects">
          Projects
        </Link>
        <Link className={styles.linkNav} to="/cards">
          Technologies
        </Link>
      </nav>
      {!photoData.url ? (
        <div className={styles.nasaMasterContainer}>
          <div className={styles.nasaPhotoContainer}>
            {photoData.media_type === "image" ? (
              <img src={photoData.url} alt="universephoto" />
            ) : (
              <iframe
                title="space-video"
                source={photoData.url}
                frameBorder="0"
                gesture="media"
                allow="encrypted-media"
                allowFullScreen
                className={styles.photo}
              />
            )}
          </div>
          <div className={styles.nasaTextContainer}>
            <h1>{photoData.title.split(" ").join("")}</h1>
            <p>{photoData.date}</p>
            <p>{photoData.explanation}</p>
          </div>
          {/* <button className = {styles.nasaExplicatieButon} onClick = {() => setShowData(!showData)} >{showData ? 'Close Explanation' : 'Explanation'}</button> */}
          <button
            className={`${styles.nasaExplicatieButon} ${styles.nasaExplicatieButonAnimated} ${styles.nasaExplicatieButonWhite}`}
            onClick={() => setShowData(!showData)}
          >
            <img src={info} alt="explicatie poza" />
          </button>
          {showData && (
            <p className={styles.explicatieNasa}>
              ***This is the NASA API Picture Of the Day, since i am passionate
              about astronomy , i thought I should share it.(Fetching data API
              with ReactJS)
            </p>
          )}
        </div>
      ) : (
        <div className={styles.nasaMasterContainer2}>
          <div className={styles.nasaPhotoContainer}>
            {photoData.media_type === "image" ? (
              <img src={photoData.url} alt="universephoto" />
            ) : (
              <iframe
                title="space-video"
                source={photoData.url}
                frameBorder="0"
                gesture="media"
                allow="encrypted-media"
                allowFullScreen
                className={styles.photo}
              />
            )}
          </div>
          <div className={styles.nasaTextContainer}>
            <h1 className = {styles.clasaTitlu}>{photoData.title.split(" ").join(".")}</h1>
            <p>{photoData.date}</p>
            <p>{photoData.explanation}</p>
          </div>
          {/* <button className = {styles.nasaExplicatieButon} onClick = {() => setShowData(!showData)} >{showData ? 'Close Explanation' : 'Explanation'}</button> */}
          {/* <button
            className={`${styles.nasaExplicatieButon} ${styles.nasaExplicatieButonAnimated} ${styles.nasaExplicatieButonWhite}`}
            onClick={() => setShowData(!showData)}
          >
            <img src={info} alt="explicatie poza" />
          </button> */}
          {showData && (
            <p className={styles.explicatieNasa2}>
              ***This is the NASA API Picture Of the Day, since i am passionate
              about astronomy , i thought I should share it.(Fetching data API
              with ReactJS)
            </p>
          )}
        </div>
      )}
    </React.Fragment>
  );
};
export default Curiosity;
